@tailwind base;
@tailwind components;
@tailwind utilities;

.match-info {
  @apply mt-1;
}

.match-tag {
  @apply text-xs inline-block rounded-full px-2 py-1 mr-1 mb-1;
  @apply bg-gray-200 text-gray-700;
}

.dark .match-tag {
  @apply bg-gray-700 text-gray-200;
}

.suggestions {
  margin-top: 2rem;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 8px;
}

body {
  @apply text-gray-800 dark:text-white;
}

html.light {
  background-color: white;
  color: black;
}

html.dark {
  background-color: #121212;
  color: white;
}

.result-item {
  /* Material Design 3 스타일 카드 */
  @apply bg-[#f4eff4] dark:bg-[#1c1b1f] dark:shadow-md p-3 sm:p-4 rounded-lg w-full max-w-2xl mx-auto text-left;
}
.no-result {
  @apply text-red-500 p-4;
}
.period-text {
  /* 다크 모드에서 가독성을 높이도록 글자색을 한 단계 밝게 */
  @apply text-sm text-gray-700 dark:text-gray-200;
}
.eligible-date {
  @apply text-sm sm:text-right;
}
